import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Navbar, Nav, NavDropdown, Button, Container, Row, Col } from 'react-bootstrap'
import { DropDownLinks, NavbarLinks, ButtonLinks, SocialMediaLinks, InfoData, OppData } from './vars/HeaderVars';
import profile from '../images/profile.jpg';

class Navigation extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }    
    render () {
        return (
            <React.Fragment>
                <Navbar variant="dark" expand="lg" fixed="top">
                    <Container>
                        <Navbar.Brand href="#home" className="nb-sansita active">I Am Arien</Navbar.Brand>
                        <Navbar.Toggle className="p-0 border-0" aria-controls="basic-navbar-nav"/>
                        <Navbar.Collapse className="offcanvas-collapse" id="basic-navbar-nav">
                            <Nav className="mr-auto">
                            {NavbarLinks.map((links, i) =>
                                <Nav.Link key={i} href={links.href} className={links.class}>
                                    {links.title}
                                </Nav.Link>
                            )}
                            <NavDropdown title="More" id="basic-nav-dropdown" className="nbl-pt-sans">
                                {DropDownLinks.map((links, i) =>
                                    <NavDropdown.Item key={i} href={links.href}>
                                        <FontAwesomeIcon icon={links.icon}/> {links.title}
                                    </NavDropdown.Item>
                                )}
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#contribute">Contribute</NavDropdown.Item>
                            </NavDropdown>
                            </Nav>
                            {ButtonLinks.map((links, i) =>
                                <React.Fragment key={i}>                                    
                                    <a href={links.href}><Button variant={links.variant} size={links.size} className={links.class}>
                                        <FontAwesomeIcon icon={links.icon}/> {' '} {links.title}
                                    </Button></a>
                                </React.Fragment>                                
                            )}
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
                <header className={this.state.class} id={this.state.id}>
                    <Container>
                        <Row>
                            <Col className="d-flex justify-content-center" lg={4} md={4} sm={12} xs={12}>                                
                                <Row>
                                    <Col className="d-flex justify-content-center" lg={12} md={12} sm={12} xs={12}>
                                        <div className="text-container">
                                            <img className="img-fluid rounded-circle image-figure-config mx-auto d-block" alt="profile" src={profile}/>                                            
                                            <p className="p-desc">                                        
                                            <b className="b-info">Hello Kamusta! <br/> I'm Norman Palisoc</b><br/>
                                            <b className="p-desc-v2">A Full-Stack Web and Mobile Developer specializing in Python and Software Development.</b>
                                            </p>
                                        </div>
                                    </Col>
                                    <Col className="d-flex justify-content-center" lg={12} md={12} sm={12} xs={12} style={{marginTop: '-70px'}}>
                                        <div className="text-container">
                                            {SocialMediaLinks.map((links, i) =>
                                                <Button key={i} size="lg" className={links.class} variant={links.variant}
                                                    onClick={()=>window.open(links.link,'_blank')}>
                                                    <i className={links.faIcon}></i>
                                                </Button>
                                            )}
                                        </div>
                                    </Col>
                                </Row>
                            </Col>
                            <Col className="d-flex justify-content-center" lg={8} md={8} sm={12} xs={12}>
                                <div className="text-container">
                                    <h2 className="h2-header-title">About <b>Norman</b></h2>
                                    <div className="div-border-about"></div>
                                    <p className="p-desc-v3">
                                        Young and talented professional by heart with extensive experience in front-end and back-end development.
                                        A competitive programmer, specializes in creating mobile applications using JAVA, Swift, Kotlin and REST APIs,
                                        building websites using HTML/CSS/PHP and integrating frameworks including Laravel, VUE | React | Node | Express JS and Python Flask | DJango,
                                        passionate in structuring relational and hierarchical databases such as MySQL, PostgreSQL, and Firebase DB. Proactive and
                                        goal oriented with top strengths in coordinating projects and solving computer, operational or team issues.
                                    </p>
                                    <Row>
                                        <Col lg={6} md={6} sm={12} xs={12}>
                                            <ul className="list-group list-group-flush">
                                                {InfoData.map((links, i) => <li className="list-group-item" key={i}><i className={links.iClass}></i>&nbsp;&nbsp;{links.title}<i className={links.iClass2}>{links.value}</i></li>)}
                                            </ul>
                                        </Col>
                                        <Col lg={6} md={6} sm={12} xs={12}>
                                            <ul className="list-group list-group-flush">
                                                {OppData.map((links, i) => <li className="list-group-item" key={i}><i className={links.iClass}></i>&nbsp;&nbsp;{links.title}</li>)}
                                            </ul>
                                        </Col>
                                    </Row><br/>
                                    <p className="p-desc-v3"><i>" Did I fit the job position you're looking ? "</i></p>
                                    <div className="text-center">
                                        <Button variant="light" className="button-hire nbl-pt-sans"
                                            onClick={()=>window.open('mailto:palisocnorman@gmail.com')}>Hire Me Now</Button>
                                    </div>                       
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </header>
            </React.Fragment>
        )
    }
}

export default Navigation;