import React, { Component } from 'react';
import { Col, Container, Row, Button } from 'react-bootstrap';

class Contact extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }
    render() {
        return (
            <React.Fragment>
                <div id={this.state.id} className={this.state.class}>
                    <Container>
                        <Row>
                            <Col lg={6} md={6} sm={12} xs={12} className="justify-content-center align-self-center align-items-center">
                                <div className="contact-information">
                                    <h2 className="h2-contact"><b>Contact</b></h2>
                                    <div className="div-border-contact"></div>
                                    <p className="contact-info">
                                        Ping me a message, ask me about something, book me an appointment or send me a direct message via email or messenger.
                                    </p>
                                    <Button variant="outline-light" size="lg" className="btn-styled"
                                        onClick={()=>window.open('mailto:palisocnorman@gmail.com')}>
                                        Via Email
                                    </Button>&nbsp;&nbsp;&nbsp;
                                    <Button variant="outline-light" size="lg" className="btn-styled"
                                        onClick={()=>window.open('https://www.facebook.com/messages/t/blackhat1999', '_blank')}>
                                        Via Messenger
                                    </Button>  
                                </div>
                            </Col>
                            <Col lg={6} md={6} sm={12} xs={12} className="justify-content-center">
                                <div className="contact-form">
                                    <form method="POST">
                                        <div className="form-group">
                                            <label htmlFor="fullname" className="lbl-name">Full Name</label>
                                            <input type="text" className="form-control form-styled" id="fullname" required="required" placeholder="e.g. Juan Dela Cruz ..." />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="email" className="lbl-name">Email Address</label>
                                            <input type="email" className="form-control form-styled" id="email" required="required" placeholder="e.g. juandelacruz@gmail.com ..." />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="desc" className="lbl-name">Description</label>
                                            <textarea className="form-control form-styled" rows="4" required="required" placeholder="e.g. Appointments, Hiring, Collaborations etc."></textarea>
                                        </div>
                                        <Button variant="success" size="lg" type="submit" className="btn-styled">
                                            Submit
                                        </Button>&nbsp;&nbsp;&nbsp;
                                        <Button variant="danger" size="lg" type="reset" className="btn-styled">
                                            Reset
                                        </Button>
                                    </form>
                                </div>                                
                            </Col>
                        </Row>
                    </Container>
                </div>
            </React.Fragment>
        )
    }
}

export default Contact;